import { useMutation, useQuery, useQueryClient, UseQueryOptions } from 'react-query'

import {
  AddAuthorizerInstanceResponse,
  AuthorizerInstance,
  AuthorizerNameAvailableResponse,
} from '../../common/types'
import { useInternalClient } from '../clients/InternalClient'
import {
  MutationKeys,
  QueryKeys,
  ReactQueryMutationHooks,
  useMutationOptions,
} from '../types/local/general'
import { RpcStatus } from '../types/local/tenant'

export const useAuthorizerInstances = (
  options?: Omit<
    UseQueryOptions<
      AddAuthorizerInstanceResponse,
      RpcStatus,
      AddAuthorizerInstanceResponse,
      QueryKeys[]
    >,
    'queryKey' | 'queryFn' | 'retry' | 'staleTime'
  >
) => {
  const { get } = useInternalClient()

  return useQuery(
    [QueryKeys.AuthorizerInstances],
    (): Promise<AddAuthorizerInstanceResponse> => get({ path: 'authorizers' }),
    {
      ...options,
      enabled: options?.enabled,
      retry: false,
      staleTime: Infinity,
    }
  )
}

export const useAddAuthorizerInstance = (
  reactQueryHooks: ReactQueryMutationHooks<AuthorizerInstance, {}> = {}
) => {
  const mutationOptions = useMutationOptions(reactQueryHooks)
  const { post } = useInternalClient()
  const queryClient = useQueryClient()

  return useMutation(
    [MutationKeys.AddAuthorizerInstance],
    (body) =>
      post({
        body,
        path: `authorizers`,
      }),
    {
      ...mutationOptions,
      onSuccess: (data, context) => {
        queryClient.invalidateQueries([QueryKeys.AuthorizerInstances])
        queryClient.removeQueries([QueryKeys.IsAuthorizerInstanceNameAvailable, data.name])
        mutationOptions?.onSuccess?.(data, context)
      },
    }
  )
}

export const useRemoveAuthorizerInstance = (
  mutationHooks: ReactQueryMutationHooks<{}, string> = {}
) => {
  const queryClient = useQueryClient()
  const mutationOptions = useMutationOptions(mutationHooks, QueryKeys.PoliciesData)
  const { del } = useInternalClient()
  return useMutation((name) => del<{}>({ path: `authorizers/${name}` }), {
    ...mutationOptions,
    onSuccess: (data, context) => {
      queryClient.removeQueries([QueryKeys.AuthorizerInstances])
      mutationOptions?.onSuccess?.(data, context)
    },
  })
}

export const useIsAuthorizerInstanceNameAvailable = (
  instanceName: string,
  options?: Omit<
    UseQueryOptions<
      AuthorizerNameAvailableResponse,
      RpcStatus,
      AuthorizerNameAvailableResponse,
      QueryKeys[]
    >,
    'queryKey' | 'queryFn' | 'retry' | 'staleTime'
  >
) => {
  const { get } = useInternalClient()
  return useQuery(
    [QueryKeys.IsAuthorizerInstanceNameAvailable, instanceName as QueryKeys],
    () =>
      get<AuthorizerNameAvailableResponse>({
        path: `authorizers/available/${instanceName}`,
      }),
    {
      ...options,
      enabled: !!instanceName && options?.enabled,
      retry: false,
      staleTime: Infinity,
    }
  )
}
