import React from 'react'
import { useState } from 'react'
import { useNavigate } from 'react-router-dom'
import { Button, SpinnerToggle, useShowError } from '@aserto/console-common'

import { AuthorizerInstance } from '../../../../common/types'
import { useRemoveAuthorizerInstance } from '../../../api/internal'
import DeleteConfirmationModal from '../../DeleteConfirmationModal'
import AuthorizerModal from '../AuthorizerModal'
import { ActionsContainer, ButtonsContainer } from './styles'

export type ManageAuthorizerProps = {
  authorizerInstance: AuthorizerInstance
}

export const ManageAuthorizer: React.FC<ManageAuthorizerProps> = ({ authorizerInstance }) => {
  const [showDeleteModal, setShowDeleteModal] = useState(false)
  const [showEditAuthorizerModal, setShowEditAuthorizerModal] = useState(false)
  const [isLoading, setIsLoading] = useState(false)
  const showError = useShowError()
  const navigate = useNavigate()
  const { mutateAsync: removeInstance } = useRemoveAuthorizerInstance({
    onSuccess: () => {
      navigate('/ui/authorizers')
    },
    onError: (error) => {
      setIsLoading(false)
      showError(error)
    },
    onMutate: () => setIsLoading(true),
  })

  const onClickRemoveAuthorizer = (instanceName: string) => {
    removeInstance(instanceName)
  }

  const onClickShowDeleteModal = () => {
    setShowDeleteModal(true)
  }

  const onClickEditAuthorizer = () => {
    setShowEditAuthorizerModal(true)
  }

  return (
    <>
      <SpinnerToggle show={isLoading} />
      <AuthorizerModal
        authorizerInstance={authorizerInstance}
        show={showEditAuthorizerModal}
        onHide={() => setShowEditAuthorizerModal(false)}
      />
      <DeleteConfirmationModal
        redirectUrl="/"
        show={showDeleteModal}
        subject="Authorizer"
        onClickRemove={() => onClickRemoveAuthorizer(authorizerInstance.name)}
        onHide={() => setShowDeleteModal(false)}
      />
      <ActionsContainer>
        <ButtonsContainer>
          <Button
            data-testid="add-authorizer-btn"
            id="add-a-authorizer"
            onClick={onClickEditAuthorizer}
          >
            Edit
          </Button>
          <Button
            data-testid="delete-authorizer"
            disabled={!authorizerInstance || showDeleteModal}
            variant="danger"
            onClick={onClickShowDeleteModal}
          >
            Delete
          </Button>
        </ButtonsContainer>
      </ActionsContainer>
    </>
  )
}
