import React from 'react'
import { Button, CardModal } from '@aserto/console-common'

import { ButtonsContainer, ContentContainer, TextArea } from './styles'

export type DeleteConfirmationModalProps = {
  redirectUrl: string
  show: boolean
  onHide: () => void
  subject: string
  onClickRemove: () => void
}

const DeleteConfirmationModal: React.FC<DeleteConfirmationModalProps> = ({
  onHide,
  show,
  subject,
  onClickRemove,
}) => {
  const onClickHideButton = onHide

  return (
    <>
      <CardModal
        cardHeight="100%"
        show={show}
        title={`Delete ${subject}`}
        variant="delete_danger"
        onHide={onClickHideButton}
      >
        <ContentContainer>
          <TextArea>Are you sure you want to delete your {subject}?</TextArea>
          <ButtonsContainer>
            <Button variant="secondary" onClick={onClickHideButton}>
              Cancel
            </Button>
            <Button data-testid="delete-subject" variant="danger" onClick={onClickRemove}>
              Delete
            </Button>
          </ButtonsContainer>
        </ContentContainer>
      </CardModal>
    </>
  )
}

export default DeleteConfirmationModal
