import copy from 'copy-to-clipboard'
import React, { useEffect, useMemo, useState } from 'react'
import { useParams } from 'react-router-dom'
import { ActionableInput } from '@aserto/console-common'

import { useAuthorizerInstances } from '../../../../api/internal'
import { ManageAuthorizer } from '../../../../components/authorizers/ManageAuthorizer'
import { useConfig } from '../../../../services/ConfigProvider'
import Frame from '../../Frame'
import { InputContainer } from './styles'

const Settings: React.FC = () => {
  const { id: authorizerInstance } = useParams()
  const { asertoDirectoryUrl, directoryApiKey, directoryTenantId } = useConfig()
  const [authorizerServiceUrl, setAuthorizerServiceUrl] = useState('')
  const [authorizerApiKey, setAuthorizerApiKey] = useState('')

  const { data } = useAuthorizerInstances()

  const authorizerInstances = useMemo(() => {
    return data?.results || []
  }, [data?.results])

  const activeInstance = useMemo(() => {
    return authorizerInstances?.find((e) => e.name === authorizerInstance)
  }, [authorizerInstances, authorizerInstance])

  useEffect(() => {
    setAuthorizerApiKey(activeInstance?.apiKey || '')
    setAuthorizerServiceUrl(activeInstance?.url || '')
  }, [activeInstance])

  return (
    <Frame>
      <div>
        <InputContainer>
          <ActionableInput
            defaultValue={authorizerInstance}
            disabled
            label="Authorizer Instance Name"
            readOnly={true}
            onClickCopy={copy}
          />
        </InputContainer>
        <InputContainer>
          <ActionableInput
            disabled
            label="Authorizer Service URL"
            readOnly={true}
            value={authorizerServiceUrl}
            onClickCopy={copy}
          />
        </InputContainer>
        <InputContainer hidden={!authorizerApiKey}>
          <ActionableInput
            disabled
            label="Authorizer API key"
            placeholder="****************************************************************"
            readOnly={true}
            shouldShowHideShowButton
            type="password"
            value={authorizerApiKey}
            onClickCopy={copy}
          />
        </InputContainer>
        <InputContainer>
          <ActionableInput
            defaultValue={asertoDirectoryUrl}
            disabled
            label="Directory Service URL"
            onClickCopy={copy}
          />
        </InputContainer>
        {directoryApiKey && directoryApiKey !== '' && (
          <InputContainer>
            <ActionableInput
              defaultValue={directoryApiKey}
              disabled
              label="Directory API key"
              placeholder="****************************************************************"
              shouldShowHideShowButton
              type="password"
              onClickCopy={copy}
            />
          </InputContainer>
        )}
        {directoryTenantId && directoryTenantId !== '' && (
          <InputContainer>
            <ActionableInput
              defaultValue={directoryTenantId}
              disabled
              label="Directory Tenant ID"
              onClickCopy={copy}
            />
          </InputContainer>
        )}

        <ManageAuthorizer
          authorizerInstance={{
            apiKey: authorizerApiKey,
            name: authorizerInstance!,
            url: authorizerServiceUrl!,
          }}
        ></ManageAuthorizer>
      </div>
    </Frame>
  )
}

export default Settings
